    export default {
        data() {
            return {
                teacher_id: '', //  老师的id
                classList: [], //所有班级列表
                gradeArr: [], //  年级
                gradeIndex: "", //年级索引
                gradeName:"",
                classArr: [], //  特定年级下的班级
                classIndex: "", //班级索引
                className:""
            };
        },
        onLoad(data) {
			this.teacher_id = uni.getStorageSync("uid")
			uniCloud.callFunction({
			    name: 'getClassList',
			})
			.then(res => {
			    console.log(res);
			    this.classList = res.result
			    this.gradeArr = res.result.gradeList
				if (uni.getStorageSync('class_id')) {
					uni.showModal({
						title:'提示',
						content:'您已经设置过班级信息了，真的要重新绑定吗？',
						success: (res) => {
							if (res.cancel) {
								this.go_back()
							}
						}
					})
				}
			})
			.catch(err => {
			    uni.hideLoading();
			    console.error(err);
			});
        },
        methods: {

            bindPickerChange: function(e) {

                console.log(e.target.value)

                this.gradeIndex = e.target.value
                this.gradeName = this.gradeArr[this.gradeIndex].name
                this.classArr = this.classList[this.gradeArr[this.gradeIndex]._id]
            },

            bindClassChange: function(e) {
                this.classIndex = e.target.value
                this.className = this.classArr[this.classIndex].name
            },

            bind() {
                uni.showLoading({})
                uniCloud.callFunction({
                        name: 'teacherBind',
                        data: {
                            grade_id: this.gradeArr[this.gradeIndex]._id,
                            class_info: this.classArr[this.classIndex]._id,
                            token: uni.getStorageSync("token"),
                            uid: uni.getStorageSync("uid"),
                            userType: uni.getStorageSync("userType")
                        }
                    })
                    .then(res => {
                        uni.hideLoading();
                        console.log(res);
                        uni.showToast({
                            title: '绑定成功',
                            duration: 2000
                        });
                    })
                    .catch(err => {
                        uni.hideLoading();
                        console.error(err);
                    });

            },
			go_back: function() {
				if (getCurrentPages().length > 1) {
					uni.navigateBack();
				} else {
					uni.switchTab({
						url: '../index/index'
					})
				}
			},
        }
    };
